<div><v-layout><v-app-bar id="color" absolute style="overflow: visible"><v-app-bar-nav-icon class="navbarcolor" @click="clickdrawer()"></v-app-bar-nav-icon><v-app-bar-title><a>Mes ralisations</a></v-app-bar-title></v-app-bar><v-navigation-drawer class="overflow-" id="color" v-model="drawer" absolute temporary><v-list nav dense><div v-for="item in redirectitems"><v-list-item :prepend-icon="item.icon" :title="item.title" @click="redirectnavbar(item.path)"></v-list-item></div></v-list></v-navigation-drawer><v-main><v-card class="mx-auto overflow-hidden" height="100vh" width="0vw"></v-card></v-main><div id="main"><v-card id="ecartcategorie" elevation="0"></v-card><v-card id="ecartcategorie" elevation="0"></v-card><div style="width: 90vw;"><v-card width="80%"><v-card-title><a>Mes Ralisations</a></v-card-title><v-card elevation="0	"><v-card-title><a>Last Voxes</a></v-card-title><v-card-text><p>Last Voxes est un bot discord permettant l'automatisation et la simplification de nombreuses taches administratives sur un serveur discord.</p><p>Sa structure est simple:</p><li>Un robot discord, dvelopp avec la librairie discord.py, permettant l'utilisation simplifie de l'API Discord, li  une base SQLite.</li><li>Utilisation de Github pour grer les versions.</li><br><p>En cours de finalisation :</p><li>Une API Python Flask (temporaire) permettant de communiquer avec une base de donnes PostgreSQL.</li><li>Une base de donnes PostgreSQL permettant de stocker les informations.</li><li>Une interface web, faite avec Vue.JS (framework HTML/ CSS / JS)</li><li>Docker pour crer des images, Kubernetes et Prometheus pour rendre l'API scallable en attendant de changer le langage.</li><br><p>Le code sur github est en repository priv, pour des raisons de scurit.</p><br><p>Pour en savoir plus  propos du bot:</p><li> <a href="https://discord.gg/bbqyjUu8dB" target="_blank">Lien vers le serveur discord (cration de compte requis)</a></li><li><a href="https://discord.com/api/oauth2/authorize?client_id=864281572399251488&amp;permissions=1480152508918&amp;scope=bot%20applications.commands" target="_blank">Lien pour inviter le robot (cration de compte + serveur discord requis)</a></li><li> <a href="https://top.gg/bot/864281572399251488" target="_blank">Lien vers top.gg, site de recensement de robots discord</a></li><br><p>Nombre de serveurs qui l'utilisent: {{lastvoxesnumguilds}}.</p></v-card-text></v-card></v-card><v-card id="ecartcategorie" elevation="0"></v-card><v-card width="80%"><v-card-title><a>Mes contributions</a></v-card-title><v-card elevation="0"><v-card-title><a>Helper Python et Base de donnes</a></v-card-title><v-card-text><p>Je suis helper Python et base de donnes sur un serveur discord d'environ 600 membres.</p><br><p>Ce genre d'exprience m'a permis de rencontrer des dveloppeurs en devenir, de discuter de diffrents projets avec eux, ce qui m'a permis<a> de rencontrer des personnes ayant de meilleures comptences que moi, qui se sont intersses  mes projets, et m'ont normment aid.</a></p><p>tre helper me permet de rendre la pareille  d'autres futurs dveloppeurs,  m'interesser  leurs projets, mais surtout, c'est pouvoir discuter autour d'un mme et unique domaine.</p></v-card-text></v-card><v-card elevation="0"><v-card-title><a>Confrence</a></v-card-title><v-card-text><p>Animation d'une confrence base sur le Python, en particulier sur la librairie discord.py, permettant de crer des robots discord.</p><br><p>J'y ai expliqu, avec un autre dveloppeur, comment crer et coder un bot discord, librairie asynchrone, dfinir une instance, diffrentes commandes, venements, mais surtout, comment lire et comprendre une documentation, ce qui n'est pas toujours facile !</p></v-card-text></v-card><v-card elevation="0"><v-card-title><a>Missions de prestations</a></v-card-title><v-card-text><p>Diffrentes missions lies au dveloppement, par exemple:</p><li>Cration d'un script permettant de modifier facilement des milliers de fichiers (JSON).</li><li>Explication de cours et d'exercices  diffrents tudiants.</li><li>Ralisation d'un bot python permettant de poster chaque nouvelle annonce poste sur dealabs.</li><li>etc.</li></v-card-text></v-card></v-card></div></div></v-layout></div>